.section {
  background-color: #f5f5f5;
}
.services {
  display: flex;
  flex-wrap: wrap;
  padding: 70px 0;
}
.card {
  width: 25%;
  height: auto;
  text-align: center;
  margin: 30px 0;
  &__img {
    display: inline-block;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 29px;
  }
  &__text {
    text-transform: uppercase;
    color: #191919;
    margin-bottom: 19px;
    font-family: "Montserrat", sans-serif;
    font-size: 12px;
    font-weight: 700;
  }
  &__subtext {
    color: #787878;
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    line-height: 1.5;
    max-width: 80%;
    margin: 0 auto;
  }
}

@media (max-width: 960px) {
  .card {
    width: 50%;
    margin: 15px 0;
    &__subtext {
      font-size: 12px;
    }
    &__text {
      margin-bottom: 10px;
    }
    &__img {
      margin-bottom: 15px;
    }
  }
  .services {
    padding: 50px 0;
  }
}
